// ===================team modal window=====================

.backdrop {
  position: fixed;
  display: inline-block;

  top: 0;
  left: 0;
  bottom: 0;
  right: 0;

  background-color: rgba(0, 0, 0, 0.5);

  border-radius: 4px;

  transition: background-color 250ms cubic-bezier(0.4, 0, 0.2, 1);
  transition: opacity 250ms cubic-bezier(0.4, 0, 0.2, 1);

  z-index: 20;
}

.modal {
  position: absolute;
  padding: 0;
  top: 50%;
  left: 50%;

  padding: 20px;

  -webkit-transform: translate(-50%, -50%);
  transform: translate(-50%, -50%);
  background-color: #fff;
  border-radius: 4px;
  -webkit-transition: background-color 250ms cubic-bezier(0.4, 0, 0.2, 1);
  transition: background-color 250ms cubic-bezier(0.4, 0, 0.2, 1);
  z-index: 20;

  align-items: flex-end;

  min-width: 300px;
  max-height: 600px;

  overflow: auto;

  @media screen and (min-width: 768px) {
    padding: 30px;
    min-width: 700px;
    max-height: 700px;
  }
}

.modal-close_btn {
  position: absolute;
  top: 8px;
  right: 8px;

  display: flex;
  justify-content: center;

  padding: 3px;

  background-color: transparent;
  border: none;
  border-radius: 50%;
  border: 1px solid #000;
}

.hidden {
  position: absolute;
  width: 1px;
  height: 1px;
  margin: -1px;
  border: 0;
  padding: 0;
  white-space: nowrap;
  clip-path: inset(100%);
  clip: rect(0 0 0 0);
  // overflow: hidden;
}

.backdrop.is-hidden {
  display: none;
  opacity: 0;

  pointer-events: none;
  visibility: hidden;

  transition: opacity 250ms cubic-bezier(0.4, 0, 0.2, 1);
}

.backdrop.is-open {
  opacity: 1;
  pointer-events: all;
  visibility: visible;

  transition: opacity 250ms cubic-bezier(0.4, 0, 0.2, 1);
}

.naw-svg {
  fill: black;
}

// ======== unit ========= //
.super-team {
  display: flex;
  flex-direction: column;
  align-items: center;
  margin: -10px;
  @media screen and (min-width: 768px) {
    margin: -20px;
    flex-wrap: wrap;
    flex-direction: row;
  }
}

.unit {
  height: auto;
  width: 100%;
  margin: 10px;

  position: relative;

  border: 1px solid #eeeeee;

  transition: background-color 250ms cubic-bezier(0.4, 0, 0.2, 1),
    opacity 250ms cubic-bezier(0.4, 0, 0.2, 1),
    box-shadow 250ms cubic-bezier(0.4, 0, 0.2, 1);

  @media screen and (min-width: 768px) {
    margin: 20px;
    width: calc((100% - 80px) / 2);
  }

  &:hover {
    box-shadow: 0px 1px 1px rgba(0, 0, 0, 0.12), 0px 4px 4px rgba(0, 0, 0, 0.06),
      1px 4px 6px rgba(0, 0, 0, 0.16);
  }

  &:hover .unit-thumb {
    transform: translateY(0);

    opacity: 1;

    background-color: #fff;
  }
}

.unit-img {
  display: inline-block;

  width: 45%;
  height: 100%;

  @media screen and (min-width: 768px) {
    width: 100%;
  }
}

.unit-info {
  font-family: 'Roboto';
  font-weight: 400;
  font-size: 10px;
  line-height: 1.4285;
  font-style: normal;

  color: #545454;

  // width: 100px;

  margin-left: auto;
  margin-right: auto;

  @media screen and (min-width: 768px) {
    font-size: 28px;
    line-height: 1.4285;
  }
}

.lostil {
  display: none;
}

// unit thumb

.unit-thumb {
  @media screen and (min-width: 768px) {
    bottom: 0;
    position: absolute;
    top: 25%;

    opacity: 0;

    transform: translateY(101%);
    transition: transform 250ms cubic-bezier(0.4, 0, 0.2, 1);
  }
}
